.image-block {
    background-image: url("https://images.unsplash.com/photo-1540221652346-e5dd6b50f3e7?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1469&q=80");
    background-size: cover;
    display: flex;
    min-height: 80vh;
    max-height: 90vh;
    z-index: -2;
    position: relative;
    justify-content: center;
}

@media screen and (max-width: 800px) {
    .image-block {
        min-height: 60vh;
        max-height: 70vh;
    }
}

.image-block::before {
    content: "";
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    position: absolute;
    height: 100%;
    z-index: -1;
    width: 100%;
    background-color: rgba(195,	142, 199, 0.3);
}

.image-block__title {
    z-index: 0;
    margin-top: 80px;
    font-size: 40px;
    text-align: center;
    color: #FFFFFF;
}

@media screen and (max-width: 800px) {
    .image-block__title {
        margin-top: 140px;
        font-size: 35px;
        padding-right: 5px;
        padding-left: 5px;
    }
}

@media screen and (max-width: 500px) {
    .image-block__title {
        font-size: 30px;
    }
}
