.previews {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 300px);
    gap: 20px;
    max-width: 100vw;
    padding-top: 40px;
    padding-bottom: 40px;
}

@media screen and (max-width: 800px) {
    .previews {
        display: flex;
        flex-direction: column;
    }
}
